import { FilterStage, FilterStageImpl } from './FilterStage'
import { ProjectStage, ProjectStageImpl } from './ProjectStage'
import { GroupStage, GroupStageImpl } from './GroupStage'
import { UnwindStage, UnwindStageImpl } from './UnwindStage'
import { SortStage, SortStageImpl } from './SortStage'
import { LimitStage, LimitStageImpl } from './LimitStage'
import { SkipStage, SkipStageImpl } from './SkipStage'
import * as apiTypes from '../../types/data-item-types'
import {
  ObjectToArrayStage,
  ObjectToArrayStageImpl,
} from './ObjectToArrayStage'

export const stages: WixDataAggregatePipelineStages = {
  filter: () => new FilterStageImpl(),
  project: () => new ProjectStageImpl(),
  group: () => new GroupStageImpl(),
  unwind: (arrayFieldName: string) => new UnwindStageImpl(arrayFieldName),
  sort: () => new SortStageImpl(),
  limit: (count: number) => new LimitStageImpl(count),
  skip: (count: number) => new SkipStageImpl(count),
  objectToArray: (objectFieldName: string, destinationFieldName: string) =>
    new ObjectToArrayStageImpl(objectFieldName, destinationFieldName),
}

/**
 * @builder
 */
export interface WixDataAggregatePipelineStages {
  filter(): FilterStage
  project(): ProjectStage
  group(): GroupStage
  unwind(arrayFieldName: string): UnwindStage
  sort(): SortStage
  limit(count: number): LimitStage
  skip(count: number): SkipStage
  objectToArray(
    objectFieldName: string,
    destinationFieldName: string
  ): ObjectToArrayStage
}

export interface PipelineStage {
  /**
   * @internal
   */
  toProto(): apiTypes.Stage
}
